.arco-icon {
  display: inline-block;
  color: inherit;
  font-style: normal;
  width: 1em;
  height: 1em;
  vertical-align: -2px;
  stroke: currentColor;
}
.arco-icon[fill='currentColor'] {
  fill: currentColor;
  stroke: none;
}
.arco-icon[stroke='currentColor'] {
  stroke: currentColor;
  fill: none;
}
.arco-icon[fill='currentColor'][stroke='currentColor'] {
  fill: currentColor;
  stroke: currentColor;
}
.arco-icon-loading {
  animation: arco-loading-circle 1s infinite cubic-bezier(0, 0, 1, 1);
}
@keyframes arco-loading-circle {
  100% {
    transform: rotate(360deg);
  }
}
.arco-icon-hover {
  position: relative;
  display: inline-block;
  cursor: pointer;
  line-height: 0;
}
.arco-icon-hover .arco-icon {
  position: relative;
  vertical-align: -0.09em;
}
.arco-icon-hover::before {
  content: '';
  position: absolute;
  display: block;
  border-radius: var(--border-radius-circle);
  background-color: transparent;
  box-sizing: border-box;
  transition: background-color 0.1s cubic-bezier(0, 0, 1, 1);
}
.arco-icon-hover:hover::before {
  background-color: var(--color-fill-2);
}
.arco-icon-hover.arco-icon-hover-disabled::before {
  opacity: 0;
}
.arco-icon-hover::before {
  top: 50%;
  left: 50%;
  height: 20px;
  width: 20px;
  transform: translate(-50%, -50%);
}
.arco-icon-hover-size-mini::before {
  top: 50%;
  left: 50%;
  height: 20px;
  width: 20px;
  transform: translate(-50%, -50%);
}
.arco-icon-hover-size-small::before {
  top: 50%;
  left: 50%;
  height: 20px;
  width: 20px;
  transform: translate(-50%, -50%);
}
.arco-icon-hover-size-large::before {
  top: 50%;
  left: 50%;
  height: 24px;
  width: 24px;
  transform: translate(-50%, -50%);
}
.arco-icon-hover-size-huge::before {
  top: 50%;
  left: 50%;
  height: 24px;
  width: 24px;
  transform: translate(-50%, -50%);
}
